.container-sch-activity {
  background-color: #344856;
  min-height: 100vh;
  width: 100%;
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-align-items: center;
  align-items: center;
}

.container-sch-activity > .header-activity {
  padding-left: 0;
}

.activity-title {
  font-family: 'europa';
  font-weight: 900;
  font-size: 30px;
  padding-left: 33px;
  padding-right: 33px;
  text-align: center;
  margin-bottom: 40px;
}

.activity-picker {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.activity-picker > .flex-center {
  /* padding-right: 20px; */
}

.activity-picker > .flex-center:last-child {
  padding-right: 0;
}

.activity-icon {
  background: #fff;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  box-shadow: 0px 10px 15px rgba(27, 28, 32, 0.1);
  padding: 2px;
}

.activity-icon-active {
  background: #d97d54;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  box-shadow: 0px 10px 15px rgba(27, 28, 32, 0.1);
  padding: 2px;
}

.activity-icon img {
  width: 35px;
}

.select-title {
  font-family: 'europa';
  font-size: 14px;
  padding-bottom: 10px;
  width: 100%;
}

.btn-bottom {
  position: absolute;
  bottom: 0;
  margin-bottom: 20px;
}

.btn-bottom {
  position: relative;
  bottom: 0;
  margin-bottom: 20px;
  margin-top: 30px;
  width: 295px;
}

.disabled {
  background-color: #c8d1d3;
}
